@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Poppins:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{width:100%;display:flex;align-items:center;height:100px}.header a{margin:0 28px;text-decoration:none;color:#181818;font-size:12px;font-weight:600;font-family:"Poppins"}.container{position:relative;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center}.container .page .inner h1{font-family:"Bebas Neue";font-size:52px;letter-spacing:0.1rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:66px}.container .page .inner p{font-family:"Poppins";margin-top:200px;width:340px;font-weight:300;line-height:24px;font-size:14px}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}

